require('./style.scss');
// const gmap = require('./gmaps/gmaps.js');

let a = [];
function rain(left){
    // console.log("fdf")
    let top = 0;
    const rain = document.createElement('div');
    const line = document.createElement('div');

    rain.style.cssText = `
        border: 1px solid rgba(255,188,107,1);
        width: 10px;
        height: 10px;
        top: ${top}px;
        left: ${left}px;
        position: absolute;
          -webkit-box-shadow: 10px 10px 80px 6px rgba(254,209,155,1);
          -moz-box-shadow: 10px 10px 80px 6px rgba(254,209,155,1);
          box-shadow: 10px 10px 80px 6px rgba(254,209,155,1);
       `
    line.style.cssText = `
        background: linear-gradient(rgba(255,188,107,0), rgba(255,188,107,1.0));
        width: 1px;
        height: 500px;
        top: ${top - 500}px;
        left: ${left + 5}px;
        position: absolute;
    `

    document.body.append(rain);
    document.body.append(line);
    const time = setInterval(()=> {
        top = top + 2;
        rain.style.top = top + 'px';
        line.style.top = top - 500 + 'px';
        if(top >= window.screen.height){
            document.body.removeChild(rain);
            document.body.removeChild(line);
            rain.remove()
            line.remove()
            window.clearInterval(time);
        }
    }, 20);
}
const window_width = window.screen.width;
console.log(window.screen.width);
// gmap.initMap();
let go = setInterval(() => {
    rain(Math.random() * window_width);
}, 6000);

